import axios from "axios";

export const CommentsDataProvider = (type, params, urlAPI) => {
  switch (type) {
    case "GET_COMMENTS":
      let uri = urlAPI + "/comments";
      let url = new URL(uri);
      if (!!params && !!params.user_id)
        url.searchParams.append("user_id", params.user_id);
      if (!!params && !!params.post_id)
        url.searchParams.append("post_id", params.post_id);
      let options = {
        method: "GET",
        url: url.toString(),
      };
      return axios(options)
        .then((res) => {
          return res.data;
        })
        .catch((error) => {
          console.log(error);
        });

    case "GET_COMMENT":
      if (!!params && !!params.comment_id) {
        uri = urlAPI + "/comments/" + params.comment_id;
        options = {
          method: "GET",
          url: uri,
        };
        return axios(options)
          .then((res) => {
            return res.data;
          })
          .catch((error) => {
            console.log(error);
          });
      }

      case "NEW_COMMENT":
        uri = urlAPI + "/comments";
        options = {
          method: "POST",
          url: uri,
          data: params,
        };
        return axios(options)
          .then((res) => {
            return res.data;
          })
          .catch((error) => {
            console.log(error);
          });


      case "GET_VOTED_COMMENTS":
        uri = urlAPI + "/comments/upvoted";
        url = new URL(uri);
        options = {
          method: "GET",
          url: url.toString(),
        };
        return axios(options)
          .then((res) => {
            return res.data;
          })
          .catch((error) => {
            console.log(error);
          });

      case "VOTE_COMMENT":
            uri = urlAPI + '/comments/'+params+'/vote';
            options = {
                method: 'POST',
                url: uri,
            }
            return axios(options).then((res) => {
                return res.data;
            }).catch((error) => {
                console.log(error);
            });
            
            
        case "UNVOTE_COMMENT":
            uri = urlAPI + '/comments/'+params+'/vote';
            options = {
                method: 'DELETE',
                url: uri,
            }
            return axios(options).then((res) => {
                return res.data;
            }).catch((error) => {
                console.log(error);
            });

    //case "LOGOUT":
    //    console.log(urlAPIAuth);
    //    break;

    default:
      throw new Error(`Unsupported Data Provider request type ${type}`);
  }
};
